<template>
  <div class="abnormalDetails full">
    <head-top headTitle = "" noneIcon = "true">
        <div slot="save" @click = "$router.go(-1)"></div>
    </head-top>
    <div class="section flex_col_left">
        <div class="con-list-sm padding">
            <light-cell lfCon = "" :rtCon = "pole.dgmc"></light-cell>
            <light-cell lfCon = "/" :rtCon = "pole.ldbmc"></light-cell>
            <light-cell lfCon = "" :rtCon = "pole.yclxShow"></light-cell>
            <light-cell lfCon = "" :rtCon = "pole.fssj"></light-cell>
            <light-cell lfCon = "" :rtCon = "now"></light-cell>
            <light-cell lfCon = "">
                <ipt-apen v-model = "repairMan" slot="other"></ipt-apen>
            </light-cell>
            <div class="text-box">
                <div class="padding_tb flex_between"><div></div></div>
                <textarea name="remarks" id="remarks" placeholder="" v-model = "txtCon"></textarea>
            </div>
        </div>
        <div class = 'padding padding_tb' style="position: fixed;bottom: 0;width: 100%">
            <div class = 'btn_submit padding_tb' @click = 'submit'></div>
        </div>
    </div> 
  </div>
</template>

<script>
import headTop from '@/components/header/mainHeader'
import lightCell from '@/components/common/lightCell'
import { hoursFormat } from '@/config/commonFun/date.js'
import iptApen from '@/components/common/iptApen'
import { Toast } from 'mint-ui'


export default {
    components:{
        headTop,
        lightCell,
        iptApen
    },
    data () {
        return {
            pole:'',
            now:hoursFormat(new Date,'{Y}-{MM}-{DD} {hh}:{ii}:{ss}'),
            repairMan:'' , //
            txtCon:'' ,//
        }
    },
    methods:{
        submit(){
            if(this.repairMan.length >= 2 && this.txtCon.length >=1){
                console.log("")
            }else{
                Toast(' /')
            }
        }
    },
    mounted() {
        this.pole = this.$route.query.item
    },
}
</script>

<style scoped lang = "scss">
#remarks{
    width: 10.2rem !important;
    height: 3rem !important ;
    background:rgba(249,249,249,1);
    border-radius:0.01rem;
    text-align: left;
    font-size:0.42rem;
}
.text-box{
    padding-bottom: 0.3rem;
}
.btn_submit{
    width: 100%;
    background: #169AFF;
    color: white;
    border-radius: 0.05rem;
}
</style>



